import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap";
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb";
import COASubCategoryTable from './Tables/COASubCategoryTable';
import COASubCategoryModal from './Modals/COASubCategoryModal';
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import COACategorySearch from '../../SearchComponents/COACategorySearch/COACategorySearch';
import COASearch from '../../SearchComponents/COASearch/COASearch';

import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';

class COASubCategory extends React.Component {
    constructor(props, context) {
        super(props, context);

        var Dropdownvalidation = 0;
        var Dropdownvalidation1 = 0;
        var Dropdownvalidation2 = 0;
        this.state = {
            errors: {},
            columns: [],
            data: [],
            filteredData: [],
            filteredCOACategoryData: [],
            filteredCOAData: [],

            value: '',
            show: false,
            showdelete: false,
            COASUBCATEGORY_ID: 0,
            COASUBCATEGORY_CODE: '',
             
            COA_ID: 0,
            COACATEGORY_ID: 0,
            SYS_STATUS_ID: 0,
            CREATEDBY: "Test M",
            MODIFIEDBY: "Test M",
            CREATEDDATE: Date.ShowDate(),
            MODIFIEDDATE: Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,
            openCOACategorysearch: false,
            openCOAsearch: false,

            SYSTEMSTATUSTYPEDESCRIPTION: '',
            COA_DESCRIPTION: '',
            COACATEGORY_CODE: '',
            searchcolumns: [],
            searchdata: [],
            searchCOACategorydata: [],
            searchCOAdata: [],

            searchFilteredData: [],
            searchvalue: '',
            searchCOACategoryvalue: '',
            searchCOAvalue: '',


        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };
      
        this.handleCOACategorySearch = () => {
            this.setState({ openCOACategorysearch: true });
        };
        this.handleCOASearch = () => {
            this.setState({ openCOAsearch: true });
        };
        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

      
        this.handleHideCOACategorySearch = () => {
            this.setState({ openCOACategorysearch: false });
        };

        this.handleHideCOASearch = () => {
            this.setState({ openCOAsearch: false });
        };


        this.populateData();
        this.populateTypes();

        this.populateCOA();

    }
    handleValidation() {

        debugger;
        let errors = {};
        let formIsValid = true;

        if (!this.state.COASUBCATEGORY_CODE) {
            formIsValid = false;
            errors["COASubCategoryCode"] = "Please Enter COA Sub Category Code";
        }
        
        if (!this.state.COA_DESCRIPTION) {
            formIsValid = false;
            errors["COADescription"] = "Please Enter COA Description";
        } if (!this.state.COACATEGORY_CODE) {
            formIsValid = false;
            errors["COACategoryCode"] = "Please Enter COA Category";
        }
       

        if (!this.state.COA_ID) {
            formIsValid = false;
            errors["COAID"] = "Please Enter COA Description";
        } if (!this.state.COACATEGORY_ID) {
            formIsValid = false;
            errors["COACategoryID"] = "Please Enter COA Category";
        }
       

        if (!this.state.SYSTEMSTATUSTYPEDESCRIPTION) {
            formIsValid = false;
            errors["Status"] = "Please Select Status";
        }
        if (!this.state.SYS_STATUS_ID) {
            formIsValid = false;
            errors["Status"] = "Please Select Status";
        }
        if (this.Dropdownvalidation === 1) {
            errors["Status"] = "";
            this.Dropdownvalidation = 0;
        }
        if (this.Dropdownvalidation1 === 1) {
            errors["COAID"] = "";
            this.Dropdownvalidation1 = 0;
        }
        if (this.Dropdownvalidation2 === 1) {
            errors["COACategoryID"] = "";
            this.Dropdownvalidation2 = 0;
        }
      
        this.setState({ errors: errors });
        return formIsValid;
    }
    populateData() {
        debugger;
        const getdata = `
        query{
            COASubCategorys{
                COASUBCATEGORY_ID
                COASUBCATEGORY_CODE
                SET_COA_CATEGORY_ID
                SET_COA_CATEGORIES{
                                COACATEGORY_ID,
                                COACATEGORY_CODE
                                            },
                SET_CHART_OF_ACCOUNT_ID
                SET_CHART_OF_ACCOUNTS{
                                COA_ID,
                                COA_DESCRIPTION
                                     },
                SYS_STATUS_ID
                SYSTEMSTATUS{
                    SYS_STATUS_ID
                    SYS_STATUS_NAME
                }
                CREATEDBY
                MODIFIEDBY
                CREATED_DATE
                MODIFIED_DATE
            }
        }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.COASubCategorys
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    populateTypes() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
                }
            })
    }

    
    populateCOACategorys(value) {
        debugger;
        const getdata = `
            query {
                COACategorysFilter(
                    SET_COA_ID:${value}
                    )
                {
                        COACATEGORY_ID,
                        COACATEGORY_CODE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchCOACategorydata: data.data.COACategorysFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Module Load Data');
                }
            })
    }

    populateCOA() {
        debugger;
        const getdata = `
            query {
                ChartOfAccounts  {
                    COA_ID,
                  COA_DESCRIPTION,
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchCOAdata: data.data.ChartOfAccounts
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Module Load Data');
                }
            })
    }


    handleSubmit = (event) => {
        event.preventDefault();
        debugger;
        if (this.handleValidation()) {
            const form = `
        mutation{
            addCOASubCategory(
                COASUBCATEGORY_CODE:"${this.state.COASUBCATEGORY_CODE}",
                SET_CHART_OF_ACCOUNT_ID:${this.state.COA_ID},
                SET_COA_CATEGORY_ID:${this.state.COACATEGORY_ID},
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                CREATEDBY:"${this.state.CREATEDBY}",
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                CREATED_DATE:"${this.state.CREATEDDATE}",
                MODIFIED_DATE:"${this.state.MODIFIEDDATE}"
                ){
                    COASUBCATEGORY_ID
            }
        }
        `;
            console.log(form);
            const formEdit = `
        mutation{
            updateCOASubCategory(
                COASUBCATEGORY_ID:${this.state.COASUBCATEGORY_ID},
                COASUBCATEGORY_CODE:"${this.state.COASUBCATEGORY_CODE}",
                SET_CHART_OF_ACCOUNT_ID:${this.state.COA_ID},
                SET_COA_CATEGORY_ID:${this.state.COACATEGORY_ID},
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                MODIFIED_DATE:"${this.state.MODIFIEDDATE}"
                ){
                    COASUBCATEGORY_ID
            }
        }
        `;

            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
    }

    handleDelete = (event) => {
        event.preventDefault();
        debugger;
        const formDelete = `
        mutation{
            deleteCOASubCategory(
                COASUBCATEGORY_ID:${this.state.COASUBCATEGORY_ID}
                ){
                    COASUBCATEGORY_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            opensearch: false,
        });
        this.Dropdownvalidation = 1;
        this.Dropdownvalidation1 = 1;
        this.Dropdownvalidation2 = 1;
        this.handleValidation();
    };


     

    handleCloseCOACategorySearch = (e) => {
        debugger;
        this.setState({
            COACATEGORY_ID: e.COACATEGORY_ID,
            COACATEGORY_CODE: e.COACATEGORY_CODE,
            openCOACategorysearch: false,
        });

    };


    handleCloseCOASearch = (e) => {
        this.setState({
            COA_ID: e.COA_ID,
            COA_DESCRIPTION: e.COA_DESCRIPTION,
            openCOAsearch: false,
        });
        this.populateCOACategorys(e.COA_ID);

    };


    handleEditData = (e) => {
        this.setState({
            COASUBCATEGORY_ID: e.COASUBCATEGORY_ID,
            COASUBCATEGORY_CODE: e.COASUBCATEGORY_CODE,
             
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SET_CHART_OF_ACCOUNT_ID: e.SET_CHART_OF_ACCOUNT_ID,
            SET_COA_CATEGORY_ID: e.SET_COA_CATEGORY_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYSTEMSTATUS.SYS_STATUS_NAME,
            COACATEGORY_CODE: e.SET_COA_CATEGORIES.COACATEGORY_CODE,
            COA_DESCRIPTION: e.SET_CHART_OF_ACCOUNTS.COA_DESCRIPTION,
            show: true,
            InsertUpdate: 'Update'
        })
        this.populateCOACategorys(e.SET_CHART_OF_ACCOUNT_ID);

    }

    handleDeleteData = (e) => {
        this.setState({
            COASUBCATEGORY_ID: e.COASUBCATEGORY_ID,
            COASUBCATEGORY_CODE: e.COASUBCATEGORY_CODE,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            COASUBCATEGORY_CODE: '',
            
            SYS_STATUS_ID: '',
            SET_CHART_OF_ACCOUNT_ID: '',
            SET_COA_CATEGORY_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            COACATEGORY_CODE: '',
            COA_DESCRIPTION: '',
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.COASUBCATEGORY_CODE.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.COASUBCATEGORY_CODE.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }
 

    handleSearchCOACategoryFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchCOACategorydata
        let filteredCOACategoryData = this.state.searchFilteredData
        this.setState({ searchCOACategoryvalue: value });

        if (value.length) {
            filteredCOACategoryData = data.filter(item => {
                let startsWithCondition =
                    item.COACATEGORY_CODE.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.COACATEGORY_CODE.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredCOACategoryData })
        }
    }
    handleSearchCOAFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchCOAdata
        let filteredCOAData = this.state.searchFilteredData
        this.setState({ searchCOAvalue: value });

        if (value.length) {
            filteredCOAData = data.filter(item => {
                let startsWithCondition =
                    item.COA_DESCRIPTION.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.COA_DESCRIPTION.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredCOAData })
        }
    }
    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdata
        let filteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }
    changeEventandValidation = (e) => {
        debugger;
        
                if (e.target.name === "COASubCategoryCode") {
                    this.setState({
                        [e.target.name]: e.target.value,
                        COASUBCATEGORY_CODE: e.target.value
                    });
                }
                if (e.target.name === "COACategoryCode") {
                    this.setState({
                        [e.target.name]: e.target.value,
                        COACATEGORY_CODE: e.target.value
                    });
                }
                if (e.target.name === "Status") {
                    this.setState({
                        [e.target.name]: e.target.value,
                        SYSTEMSTATUSTYPEDESCRIPTION: e.target.value
                    });
        
                }
                if (e.target.name === "StatusID") {
                    this.setState({
                        [e.target.name]: e.target.value,
                        SYS_STATUS_ID: e.target.value
                    });
                }
                if (e.target.name === "COADescription") {
                    this.setState({
                        [e.target.name]: e.target.value,
                        COA_DESCRIPTION: e.target.value
                    });
        
                }
                if (e.target.name === "COAID") {
                    this.setState({
                        [e.target.name]: e.target.value,
                        COA_ID: e.target.value
                    });
                }
                if (e.target.name === "COACategoryCode") {
                    this.setState({
                        [e.target.name]: e.target.value,
                        COACATEGORY_CODE: e.target.value
                    });
        
                }
                if (e.target.name === "COACategoryID") {
                    this.setState({
                        [e.target.name]: e.target.value,
                        COACATEGORY_ID: e.target.value
                    });
                }
                 
                
                this.handleValidation();
        
            };
    render() {
        const { data, value, filteredData, filteredCOACategoryData, filteredCOAData,
             searchdata, searchCOACategorydata, searchCOAdata, 
             searchvalue,  searchCOACategoryvalue, searchCOAvalue } = this.state
        return (
            <>
                <Helmet><title>COASubCategorys</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="COASubCategorys"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>COASubCategorys</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={searchvalue}
                                        data={searchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />
                                    <COACategorySearch
                                        handleSearchCOACategory={this.handleCloseCOACategorySearch}
                                        opensearch={this.state.openCOACategorysearch}
                                        handleHideSearch={this.handleHideCOACategorySearch}
                                        value={searchCOACategoryvalue}
                                        data={searchCOACategorydata}
                                        handleFilter={this.handleSearchCOACategoryFilter}
                                        filteredData={filteredCOACategoryData}
                                    />

                                    <COASearch
                                        handleSearchCOA={this.handleCloseCOASearch}
                                        opensearch={this.state.openCOAsearch}
                                        handleHideSearch={this.handleHideCOASearch}
                                        value={searchCOAvalue}
                                        data={searchCOAdata}
                                        handleFilter={this.handleSearchCOAFilter}
                                        filteredData={filteredCOAData}
                                    />
                                   


                                    <COASubCategoryTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <COASubCategoryModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}
                                        handleCOACategorySearch={this.handleCOACategorySearch}
                                        handleCOASearch={this.handleCOASearch}

                                        changeEventandValidation={this.changeEventandValidation}
                                        ErrorCOASubCategoryCode={this.state.errors["COASubCategoryCode"]}
                                        ErrorCOASubCategoryName={this.state.errors["COASubCategoryName"]}
                                        ErrorCOASubCategoryDescription={this.state.errors["COASubCategoryDescription"]}
                                        ErrorCOADescription={this.state.errors["COADescription"]}
                                        ErrorCOACategoryCode={this.state.errors["COACategoryCode"]}
                                        ErrorStatus={this.state.errors["Status"]}

                                        SystemStatusTypeID={this.state.SYS_STATUS_ID}

                                        COACATEGORY_ID={this.state.COACATEGORY_ID}

                                        COACATEGORY_CODE={this.state.COACATEGORY_CODE}

                                        COA_ID={this.state.COA_ID}

                                        COA_DESCRIPTION={this.state.COA_DESCRIPTION}

                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}

                                        COASUBCATEGORY_CODE={this.state.COASUBCATEGORY_CODE}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(COASubCategory));